import React from 'react';
import { graphql, PageProps } from 'gatsby';
import PostPage from 'containers/PostPage';
import { PostPageContext } from 'containers/PostPage/models';

const PostTemplate = (props: PageProps<Queries.PostPageQuery, PostPageContext>) => {
  return <PostPage {...props} />;
};

export default PostTemplate;

export const pageQuery = graphql`
  query PostPage($slug: String!, $lang: String!, $postGroup: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      ...Post
    }
    allMarkdownRemark(filter: { fields: { postGroup: { eq: $postGroup }, lang: { ne: $lang } } }) {
      edges {
        node {
          ...Post
        }
      }
    }
  }
`;
